.projects_div {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100dvh;
}

.projects_list {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 10px;
}

.projects_title{
    font-weight: bold;
    font-size: 1.5rem;
    margin-bottom: 5px;
}

.projects_playground {
    position: absolute;
    right: 10px;
    bottom: 10px;
}