.nav_div {
  height: 100dvh;
  width: 12.5rem;
  position: fixed;
  padding: 1.25rem;
}
.nav_title {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.themeBtn {
  all: unset;
  cursor: pointer;
  position: absolute;
  bottom: 10px;
  font-weight: bold;
  &:hover {
    background-color: var(--bg-hover);
    color: var(--text-color-hover);
  }
}
