*,
*:before,
*:after {
  box-sizing: inherit;
  -webkit-user-select: inherit;
  user-select: inherit;
  -webkit-user-drag: inherit;
  -webkit-tap-highlight-color: inherit;
  backface-visibility: inherit;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Neue Montreal Mono";
  font-weight: normal;
  font-size: 14px;
}
html {
  line-height: 1;
  box-sizing: border-box;
  -webkit-user-select: none;
  user-select: none;
  -webkit-user-drag: none;
}
a {
  text-decoration: none;
  color: inherit;
}
a {
  padding: 3px 0px;
  display: block;
  font-weight: bold;
  &:hover {
    background-color: var(--bg-hover);
    color: var(--text-color-hover);
  }
}
ol,
ul {
  list-style: none;
}

:root {
  --text-color-light: rgb(0, 0, 0);
  --text-color-dark: rgb(255, 255, 255);

  --text-color-hover-light: rgb(255, 255, 255);
  --text-color-hover-dark: rgb(0, 0, 0);

  --bg-content-light: rgb(255, 255, 255);
  --bg-content-dark: rgb(40, 40, 40);

  --bg-navbar-light: rgb(200, 200, 200);
  --bg-navbar-dark: rgb(30, 30, 30);

  --bg-hover-light: rgb(40, 40, 40);
  --bg-hover-dark: rgb(255, 255, 255);
}

.light-theme {
  --text-color: var(--text-color-light);
  --text-color-hover: var(--text-color-hover-light);
  --bg-content: var(--bg-content-light);
  --bg-navbar: var(--bg-navbar-light);
  --bg-hover: var(--bg-hover-light);
}

.dark-theme {
  --text-color: var(--text-color-dark);
  --text-color-hover: var(--text-color-hover-dark);
  --bg-content: var(--bg-content-dark);
  --bg-navbar: var(--bg-navbar-dark);
  --bg-hover: var(--bg-hover-dark);
}

@font-face {
  font-family: "Neue Montreal Mono";
  font-weight: 400;
  src: url("./fonts/PPNeueMontrealMono-Book.woff2") format("woff2");
}
@font-face {
  font-family: "Neue Montreal Mono";
  font-weight: 600;
  src: url("./fonts/PPNeueMontrealMono-SemiBold.woff2") format("woff2");
}

body {
  height: 100dvh;
  overflow: hidden;
}

.navbar,
.App {
  background-color: var(--bg-navbar);
  color: var(--text-color);
}

.content {
  background-color: var(--bg-content);
  color: var(--text-color);
  margin-left: 200px;
  padding: 20px;
  height: 100dvh;
}
